@media screen and (min-width: 1281px) {
  .event-highlights i {
    left: 40%; } }

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotateZ(0deg);
    -moz-transform: rotateZ(0deg);
    -ms-transform: rotateZ(0deg);
    -o-transform: rotateZ(0deg); }
  100% {
    -webkit-transform: rotateZ(360deg);
    -moz-transform: rotateZ(360deg);
    -ms-transform: rotateZ(360deg);
    -o-transform: rotateZ(360deg); } }

.event-highlights--circle {
  width: 150px;
  height: 150px;
  animation: spin 1.5s infinite linear;
  -webkit-animation: spin 1.5s infinite ease;
  -moz-animation: spin 1.5s infinite ease;
  -ms-animation: spin +1.5s infinite ease;
  -o-animation: spin 1.5s infinite ease;
  border-top: 5px solid #3881AA;
  border-right: 5px solid #3881AA;
  border-bottom: 5px solid #6099B7;
  border-left: 5px solid #6099B7;
  border-radius: 50%;
  background: #FFFFFF; }

.event-navigate-links:hover .event-highlights--circle {
  animation: none;
  -webkit-animation: none;
  -moz-animation: none;
  -ms-animation: none;
  -o-animation: none; }

@-moz-keyframes blinker {
    0% { opacity: 1.0; }
    50% { opacity: 0.0; }
    100% { opacity: 1.0; }
}

@-webkit-keyframes blinker {
    0% { opacity: 1.0; }
    50% { opacity: 0.0; }
    100% { opacity: 1.0; }
}

@keyframes blinker {
    0% { opacity: 1.0; }
    50% { opacity: 0.0; }
    100% { opacity: 1.0; }
}

