/*Registration Form*/
input {
  font-family: inherit;
  box-sizing: border-box;
  color: inherit; }

.devday-register {
  width: 100%;
  margin: 0 20px;
  padding: 20px 10px;
  border-radius: 5px;
  background-color: #354497; }

@media screen and (min-width: 840px) {
  .devday-register {
    padding: 10px; } }

.sign-up-input {
  font-size: 17px;
  height: 70px;
  margin: 0 0 20px 8px;
  padding: 0 15px 2px;
  text-align: center;
  border: 2px solid #EBEBEB;
  border-radius: 4px;
  background: white;
  box-shadow: inset 0 -2px #EBEBEB; }

@media screen and (min-width: 840px) {
